import React, { useState } from 'react'
import { useSetRecoilState } from 'recoil'
import ClassSelectMD from '../../../../components/common/Class/ClassSelectMD'
import { Grid } from '../../../../components/common/Grid/Grid'
import PageContainer from '../../../../components/layout/PageContainer'
import EditBtnSM from '../../../../components/UI/Atoms/Button/EditBtnSM'
import Description from '../../../../components/UI/Atoms/Ex/Description'
import Em from '../../../../components/UI/Atoms/Ex/Em'
import ProfileImage from '../../../../components/UI/Atoms/Profile/ProfileImage'
import { useDebug } from '../../../../hooks/basic/useDebug'
import { useSelect } from '../../../../hooks/basic/useSelect'
import { phoneHyphenRegex } from '../../../../utils/lib/Regex'
import { toAge } from '../../../../utils/render/toAge'
import { toGender } from '../../../../utils/render/toGender'
import { useStudent } from '../../Manage/Student/hooks/queries/useStudent'
import { studentIdAtom } from './recoil/studentIdAtom'
import RankImage from '../../../../components/common/Rank/RankImage';
import { Typography } from '@mui/material'
import DescriptionBox from '../../../../components/common/Text/DescriptionBox'

const StudentSelect = ({ setStep }) => {

  // recoil
  const setStudentId = useSetRecoilState(studentIdAtom)

  // custom hook
  const [classes, handleChangeClasses] = useSelect('0')
  
  // query
  const {data: students} = useStudent(classes)

  // handler
  const handleClickItem = e => {
    const clickId = e.target.id
    setStudentId(clickId)
    setStep(2)
  }

  // useDebug(students)

  return (
    <>
      <Typography variant='h2' className='font-bold mb-[20px]'>
        승급 대상자를 먼저 선택해주세요.
      </Typography>
      <div className='flex justify-between items-center mb-3 w-full'>
        <ClassSelectMD value={classes} onChange={handleChangeClasses} />
      </div>

      { students && 
      <Grid>
        <Grid.Head>
          <Description>현재 선택된 반의 수련생은 <Em>{students?.count}명</Em>입니다.</Description>
        </Grid.Head>
        <Grid.RowWrap>
          {React.Children.toArray(
            students?.rows?.map((item) => 
              <Grid.Row>
                <Grid.Col fixed='65px' className='pr-0'><ProfileImage urlKey={item?.photo_url}/></Grid.Col>
                <Grid.Col fixed='120px' className='pr-0'>{item?.last_name}{item?.first_name}</Grid.Col>
                <Grid.Col fixed='70px' className='pr-0'><RankImage src={item?.level} /></Grid.Col>
                <Grid.Col fixed='70px' className='pr-0'>{toGender(item?.sex)}</Grid.Col>
                <Grid.Col fixed='70px' className='pr-0'>{toAge(item?.dob)}</Grid.Col>
                <Grid.Col fixed='160px' className='pr-0'>{item?.Classes?.title}</Grid.Col>
                <Grid.Col fixed='200px' className='pr-0'>{phoneHyphenRegex(item?.phone_number)}</Grid.Col>
                <Grid.Col>
                  <EditBtnSM id={item?.id} onClick={handleClickItem} className='min-w-[70px]'>선택</EditBtnSM>
                </Grid.Col>
              </Grid.Row>
            )
          )}
        </Grid.RowWrap>
      </Grid>
      }

    </>
  )
}

export default StudentSelect