import { useQuery } from '@tanstack/react-query'
import React, { useState } from 'react'
import { useRecoilValue } from 'recoil'
import { getMyInfo } from '../../../../api/user'
import PageContainer from '../../../../components/layout/PageContainer'
import { useDebug } from '../../../../hooks/basic/useDebug'
import userInfoAtom from '../../../../recoil/atom/login/userInfoAtom'
import Confirm from './Confirm'
import StudentSelect from './StudentSelect'

const LevelupResistPage = () => {

  const userInfo = useRecoilValue(userInfoAtom);

  const [ step, setStep ] = useState(1)

  const { data } = useQuery(['getMyInfo', userInfo?.id], () => getMyInfo(userInfo.id))
  

  // useDebug(data.User_info.id)
  
  return (
    <PageContainer>
    { step === 1 &&
      <StudentSelect setStep={setStep} />
    }
    { step === 2 &&
      <Confirm setStep={setStep} kwanjangId={data?.User_info?.id}/>
    }
    </PageContainer>
  )
}

export default LevelupResistPage