import { useMutation, useQuery } from '@tanstack/react-query';
import moment from 'moment/moment';
import React, { useState } from 'react';
import { useRecoilValue } from 'recoil';
import { getInfoForLevelup, postLevelup } from '../../../../api/Levelup/levelupApi';
import AlertText from '../../../../components/common/Alert/AlertText';
import Box from '../../../../components/layout/Box';
import ModalContainerAlert from '../../../../components/layout/ModalContainerAlert';
import BlackBtnSM from '../../../../components/UI/Atoms/Button/BlackBtnSM';
import WhiteBtnSM from '../../../../components/UI/Atoms/Button/WhiteBtnMD';
import DatepickerSM from '../../../../components/UI/Atoms/Datepicker/DatepickerSM';
import InfoData from '../../../../components/UI/Atoms/Info/InfoData';
import InfoLabelMD from '../../../../components/UI/Atoms/Info/InfoLabelMD';
import AdornmentInput from '../../../../components/UI/Atoms/Input/AdornmentInput';
import ProfileImage from '../../../../components/UI/Atoms/Profile/ProfileImage';
import { useDebug } from '../../../../hooks/basic/useDebug';
import { useModal } from '../../../../hooks/basic/useModal';
import { useInput } from '../../../../hooks/input/useInput';
import dojangIdAtom from '../../../../recoil/atom/dojangIdAtom';
import { phoneHyphenRegex } from '../../../../utils/lib/Regex';
import { toGender } from '../../../../utils/render/toGender';
import { Col, Input, Label, Row, } from '../../Manage/Student/component/StudentInfo/StudentForm/StudentForm';
import { studentIdAtom } from './recoil/studentIdAtom';
import KeyboardDoubleArrowRightIcon from '@mui/icons-material/KeyboardDoubleArrowRight';
import RankImage from '../../../../components/common/Rank/RankImage';
import RankSelect from '../../../../components/UI/Molecules/Rank/RankSelect';
import { toLevelString } from '../../../../utils/basic/transform';
import { getFullname } from '../../../../utils/render/getFullname';

const Confirm = ({ setStep, kwanjangId }) => {
  const [rank, setRank] = useState('');

  // custom hook
  const [date, handleChange] = useInput('');
  const [open, openFn, closeFn] = useModal();

  // recoil
  const dojangId = useRecoilValue(dojangIdAtom);
  const studentId = useRecoilValue(studentIdAtom);

  // query
  const { data } = useQuery(['getInfoForLevelup', dojangId, kwanjangId, studentId], () => getInfoForLevelup(dojangId, kwanjangId, studentId),
    {
      enabeld: !!dojangId && !!kwanjangId && !!studentId,
    },
  );


  const { mutate: submitMt, isLoading } = useMutation((body) =>
    postLevelup(dojangId, studentId, body),
  );

  // handler
  const handleSubmit = () => {
    let postData = {
      levelUpInfo: {
        level: toLevelString(rank),
        regDate: date,
      },
      ...data,
    };

    if (date == '') {
      alert('승급일을 등록해야 승급이 가능합니다.');
      return;
    }
    if (postData.levelUpInfo.level == '') {
      alert('승급 급수를 선택해주세요.');
      return;
    } else {
      submitMt(postData, {
        onSuccess: (res) => {
          if (res == 'level after must needed') {
            alert('승급 급수를 선택해주세요.');
          }
          if (res == 'regDate must needed') {
            alert('승급일을 등록해야 승급이 가능합니다.');
          } else {
            window.location.reload();
          }
        },
      });
    }
  };

  const handleSelect = (e) => {
    setRank(e.target.value);
  };

  // useDebug(data);
  return (
    <>
      <Box
        className="border border-solid border-[1px] border-[#dddddd] px-[20px]"
        title="수련생 정보"
      >
        <div className="flex flex-col">
          <div className="flex items-center gap-[20px]">
            <ProfileImage full size="140px" urlKey={data?.student_info?.photo_url || ''} />
            <div className="flex flex-col justify-between">
              <Row>
                <Col col={1}>
                  <Label>이름</Label>
                  <Input
                    defaultValue={getFullname(
                      data?.student_info?.last_name || '',
                      data?.student_info?.first_name || '',
                    )}
                    disabled
                  />
                </Col>
                <Col col={1} className="justify-end">
                  <Label>성별</Label>
                  <Input defaultValue={toGender(data?.student_info?.sex || '')} disabled />
                </Col>
              </Row>
              <Row>
                <Col>
                  <Label>체중</Label>
                  <AdornmentInput
                    unit="kg"
                    defaultValue={data?.student_info?.weight || ''}
                    disabled
                  />
                </Col>
                <Col>
                  <Label>생년월일</Label>
                  <Input defaultValue={data?.student_info?.dob || ''} disabled />
                </Col>
              </Row>
              <Row>
                <Col>
                  <Label>전화번호</Label>
                  <Input
                    defaultValue={phoneHyphenRegex(data?.student_info?.phone_number || '')}
                    disabled
                  />
                </Col>
                <Col>
                  <Label>메일</Label>
                  <Input
                    type="email"
                    defaultValue={data?.student_info?.email || ''}
                    disabled
                  />
                </Col>
              </Row>
              <Row>
                <Col>
                  <Label>주소</Label>
                  <div className="flex justify-between w-[525px]">
                    <Input
                      defaultValue={data?.student_info?.address_name || ''}
                      disabled
                      className="min-w-[335px]"
                    />
                    <Input defaultValue={data?.student_info?.address_detail || ''} disabled />
                  </div>
                </Col>
              </Row>
            </div>
          </div>
          <div className="border border-solid border-[1px] border-t-0 border-[#dddddd] mt-[30px] mb-[14px]"></div>
          <div className="flex flex-col">
            <Row>
              <Col>
                <Label>도장 이름</Label>
                <Input
                  defaultValue={data?.dojang_info?.name || ''}
                  disabled
                  className="w-[140px]"
                />
              </Col>
              <Col>
                <Label>심사인</Label>
                <Input
                  defaultValue={getFullname(
                    data?.last_name || '',
                    data?.first_name || '',
                  )}
                  disabled
                  className="w-[140px]"
                />
              </Col>
              <Col>
                <Label>전화번호</Label>
                <Input
                  defaultValue={phoneHyphenRegex(data?.phone_number || '')}
                  disabled
                  className="w-[140px]"
                />
              </Col>
            </Row>
          </div>
        </div>
      </Box>
      <Box className="flex w-[825px] border border-solid border-[1px] border-[#dddddd] px-[20px] bg-[#fafafa] shadow py-[10px]">
        <div className="flex m-auto items-center">
          <div className="flex flex-col gap-[5px]">
            <InfoLabelMD className="text-center mb-[5px] text-[17px]">현재 급수</InfoLabelMD>
            <div className="flex items-center h-[42px] w-[160px] text-center">
              <InfoData className="w-[50px]">{data?.student_info?.level || ''}</InfoData>
              <InfoData>
                <RankImage src={data?.student_info?.level || ''} />
              </InfoData>
            </div>
          </div>
          <span className="mx-[50px]">
            <KeyboardDoubleArrowRightIcon className="text-[#555555]" />
          </span>
          <div className="flex flex-col gap-[5px]">
            <InfoLabelMD className="text-center mb-[5px] text-[17px]">승급 급수</InfoLabelMD>
            <div className="flex items-center">
              <RankSelect value={rank || ''} onChange={handleSelect} className='h-[30px]' sx={{'& .MuiInputBase-root': {border: '0'}}} />
            </div>
          </div>
        </div>
      </Box>
      <div className="w-[825px] mt-[15px] flex justify-center">
        <Row>
          <Col>
            <Label>승급일 *</Label>
            <DatepickerSM
              length={186}
              value={moment(date || '').format('YYYY-MM-DD')}
              onChange={handleChange}
            />
          </Col>
        </Row>
      </div>
      <div className="flex justify-center my-[50px] gap-[6px]">
        <WhiteBtnSM onClick={() => setStep(1)} className="mr-[2px]">
          취소
        </WhiteBtnSM>
        <BlackBtnSM onClick={openFn}>승급</BlackBtnSM>
      </div>

      <ModalContainerAlert
        open={open}
        onClose={closeFn}
        onConfirm={handleSubmit}
        confirmText="등록"
        isLoading={isLoading}
      >
        <AlertText title>승급을 합니다.</AlertText>
        <AlertText className="tracking-normal">
          '대한태권도협회'에 공식적으로 등록됩니다. <br />
          승급 등록이 되면 수정/취소가 불가능 합니다.
          <br />
          등록 하시겠습니까?
        </AlertText>
      </ModalContainerAlert>
    </>
    // <>d</>
  );
};

export default Confirm;
